
.home-cont{
    padding: 2em;
   
  }
  .home-cont nav{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .home-container{
    padding: 0 2em;
    margin: 0 2em 2em 0;
    height: 40%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap:wrap;
    
  }
  /*Left Container of home*/
  .right-container{
    width: 17em;
    padding: 2em;
    margin-left: 3em;
    background-color: #26273C;
    border-radius: 10px;
    text-align: center;
  }
  .right-light{
    width: 17em;
    padding: 2em;
    margin-left: 3em;
    border-radius: 10px;
    text-align: center;
    background-color: #f8f8ff;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
  
  .label{
    margin-top: 4em;
  }
  .Kumusta{
    font-size: clamp(18px, 1.5vw, 36px);
   
  }
  .name{
    font-size: clamp(18px, 2vw, 38px);
    
  }
  .name-light{
    
  }
  /*new*/
  .web-dev{
    font-size: clamp(18px, 2vw, 38px);
    color: #FB7171;
  }
  .contact{
    background-color: #9900F8;
    padding: 1em ;
    width: 14em;
    border-radius: 10px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    color:white;
  }
  .contact:hover{
   filter:brightness(60%);
  }
  /*Right image*/
  @keyframes expand{
    0% {width: 280px;}
    25% {width: 290px;}
    50%{transform: rotate(20deg);  }
    70%{transform: rotate(-20deg);}
    100%{width: 300px;}
  }
  @keyframes fade-in {
    0%{opacity: 100%;}
    10%{opacity: 20%;}
    40%{opacity: 40%;}
    60%{opacity: 60%;}
    80%{opacity: 80%;}
    100%{opacity: 100%;}
  }
  .Nicole{
    width: 20em;
    border-radius: 2em;
    animation: fade-in 1s;
  }
  .Nicole:hover{
    animation-name:  expand;
    animation-duration: 2s ;
  }
  
  
  
  