/*About*/
.About-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap:wrap;
    padding-top: 2em;
  }
  .about-me, .skills{
    width: 17em;
    height: 70%;
    justify-content: space-around;
    background-color:#26273C ;
    color:white;
    padding: 1em;
    border-radius: 10px;
    margin: 1em;
  }
  .about-light, .skills-light{
    width: 300px;
    height: 70%;
    justify-content: space-around;
    background-color:white ;
    color:black;
    padding: 1em;
    border-radius: 10px;
    margin: 1em;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
  
  
  #fa-envelope, #fa-download{
    color: #F0F413;
  }
  .email_add, #ClickMe{
    color: #FB7171;
  }
  .quote{
    font-weight: 700;
  }